import React, { useState } from 'react';
import { Button, InputNumber, Card, Typography, Row, Col, Alert } from 'antd';

const { Title, Text } = Typography;

function PredictionPage() {
  const [predictions, setPredictions] = useState([]);
  const [count, setCount] = useState(1);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchPredictions = () => {
    setLoading(true);
    setError(null);
    fetch(`https://lotto-backend-b8184723673f.herokuapp.com/predict?count=${count}`)
      .then((res) => {
        if (!res.ok) {
          throw new Error('   .');
        }
        return res.json();
      })
      .then((data) => {
        setPredictions(data);
        setLoading(false);
      })
      .catch((err) => {
        console.error(err);
        setError('    .   .');
        setLoading(false);
      });
  };

  return (
    <div style={{ padding: '50px' }}>
      <Title level={2} style={{ textAlign: 'center' }}>  </Title>
      <div style={{ textAlign: 'center', marginBottom: '20px' }}>
        <Text>     ?</Text>
        <div style={{ marginTop: '10px' }}>
          <InputNumber
            min={1}
            value={count}
            onChange={(value) => setCount(value)}
            style={{ marginRight: '10px' }}
          />
          <Button type="primary" onClick={fetchPredictions} loading={loading}>
            
          </Button>
        </div>
      </div>

      {error && (
        <div style={{ textAlign: 'center', marginBottom: '20px' }}>
          <Alert message={error} type="error" showIcon />
        </div>
      )}

      {predictions.length > 0 ? (
        <Row gutter={[16, 16]} justify="center">
          {predictions.map((p, idx) => (
            <Col xs={24} sm={12} md={8} lg={6} key={idx}>
              <Card title={`${idx + 1} `} bordered={true} style={{ textAlign: 'center' }}>
                <Text strong style={{ display: 'block', marginBottom: '10px' }}>
                  : {p.numbers.join(', ')}
                </Text>
                <Text type="secondary">
                  : {p.bonus}
                </Text>
              </Card>
            </Col>
          ))}
        </Row>
      ) : (
        !error && (
          <div style={{ textAlign: 'center' }}>
            <Text type="secondary"> . ""  .</Text>
          </div>
        )
      )}
    </div>
  );
}

export default PredictionPage;
