import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';

// root   
const rootElement = document.getElementById('root');

// rootElement    
if (!rootElement) {
  throw new Error('Failed to find the root element. Ensure an element with id "root" exists in your HTML.');
}

// React 18 createRoot 
const root = ReactDOM.createRoot(rootElement);

// React.StrictMode App  
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
