import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useLocation } from 'react-router-dom';
import PredictionPage from './pages/PredictionPage';
import PastResultsPage from './pages/PastResultsPage';
import { Layout, Menu } from 'antd';

const { Header, Content, Footer } = Layout;

function App() {
  return (
    <Router>
      <AppLayout />
    </Router>
  );
}

function AppLayout() {
  const selectedKey = useSelectedKey();

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Header>
        <Menu theme="dark" mode="horizontal" selectedKeys={[selectedKey]}>
          <Menu.Item key="1">
            <Link to="/">  </Link>
          </Menu.Item>
          <Menu.Item key="2">
            <Link to="/past-results">  </Link>{}
          </Menu.Item>
        </Menu>
      </Header>
      <Content style={{ padding: '20px', textAlign: 'center' }}>
        <Routes>
          <Route path="/" element={<PredictionPage />} />
          <Route path="/past-results" element={<PastResultsPage />} />
          <Route path="*" element={<h1>404:    .</h1>} />
        </Routes>
      </Content>
      <Footer style={{ textAlign: 'center' }}>
        LOTTO PLEASE {new Date().getFullYear()} Created by Hansol Choi
      </Footer>
    </Layout>
  );
}

function useSelectedKey() {
  const location = useLocation();
  switch (location.pathname.split('?')[0]) { //   
    case '/':
      return '1';
    case '/past-results':
      return '2';
    default:
      return '';
  }
}

export default App;
